
import { Options, Vue } from "vue-class-component";

@Options({
  props: {
    type: "",
    href: "",
    mime: "",
    trigger: 0,
  },
})
export default class EntryPreview extends Vue {
  dialogVisible = false;
  type = "";
  href = "";
  mime = "";

  public created(): void {
    // https://github.com/vuejs/vue-class-component/issues/406#issuecomment-784470556
    this.$watch("trigger", () => {
      this.dialogVisible = true;
    });
  }
}
