
import { Options, Vue } from "vue-class-component";

@Options({})
export default class LoginForm extends Vue {
  rules = {
    server: [{ required: true, validator: this.checkIsUrl }],
    token: [{ required: true }],
  };

  server = "";
  token = "";

  public submit(): void {
    let payload: LoginSubmitPayloadModel = {
      server: this.server,
      token: this.token,
    };

    this.$emit("submitLogin", payload);
  }

  // https://stackoverflow.com/questions/5717093/check-if-a-javascript-string-is-a-url
  private checkIsUrl(
    rule: unknown,
    value: string,
    callback: (error?: Error) => void
  ) {
    if (!value) {
      callback(new Error("server is required"));
      return;
    }

    let url;

    try {
      url = new URL(value);
    } catch (_) {
      callback(new Error("Url is invalid"));
      return;
    }
    if (url.protocol === "http:" || url.protocol === "https:") {
      callback();
    } else {
      callback(new Error("Url is invalid"));
    }
  }
}

export interface LoginSubmitPayloadModel {
  server: string;
  token: string;
}
